            <img src="/static/img/brand-65-2.png" class="center-img" />
          </div>
        </div>

        <img src="/static/img/brand-7-bg.png" class="brand-img alignBot" :class="[currentBrandNum==11?'doshow2':'dohide']" v-show="showBrandPage" />
        <img src="/static/img/brand-7-2.png" class="brand-img img72" :class="[currentBrandNum==11?'doshow2':'dohide']" v-show="showBrandPage" />
        <img src="/static/img/brand-7-1.png" class="brand-img alignTop" :class="[currentBrandNum==11?'doshow2':'dohide']" v-show="showBrandPage" />

        <img src="/static/img/brand-8-bg.png" class="brand-img alignBot" :class="[currentBrandNum==12?'doshow2':'dohide']" v-show="showBrandPage" />
        <img src="/static/img/brand-8-1.png" class="brand-img alignTop" :class="[currentBrandNum==12?'doshow2':'dohide']" v-show="showBrandPage" />
        <div class="brand-8-text" :class="[currentBrandNum==12?'doshow2':'dohide']" v-show="showBrandPage">The story of TAQWA will continue forever.</div>
        
        <div class="scroll-btn" @click="brandChangePage()">
          <img :src="`/static/img/icon_scroll${currentBrandNum>4&&currentBrandNum!=12?'1':''}.png`" class="top_arrow">
        </div>
      </div>
    </div>
    <div class="job-block absolute" v-show="initOk" :class="[currentMenu=='job'?'doshow2':'dohide']">
      <div class="swiper-container job-swiper job-swiper1 swiper-no-swiping">
        <div class="swiper-wrapper">
          <div class="job-box swiper-slide">
            <img src="/static/img/job-1-1.png" class="job-img">
            <img src="/static/img/job-1-2.png" class="job-img">
            <div class="job-btn3" @click="tabChange('contact')"></div>
          </div>
          <div class="job-box swiper-slide">
            <img src="/static/img/job-2-1.png" class="job-img">
            <img src="/static/img/job-2-2.png" class="job-img">
            <div class="job-btn3" @click="tabChange('contact')"></div>
          </div>
        </div>
      </div>
      <div class="job-center">
        <div class="job-btns">
          <img src="/static/img/job-arrow.png" class="job-btn left" @click="jobSwiperChange(-1)">
          <img src="/static/img/job-arrow.png" class="job-btn" @click="jobSwiperChange(1)">
        </div>
        <form action="contact@taqwa.io" method="post" enctype="text/plain" style="position: relative;">
          <input type="submit" style="width: 100%;height: 50px;position: absolute;left: 0;top: 0;opacity: 0;" />
          <img src="/static/img/job-email.png" class="job-img">
        </form>
        <img src="/static/img/job-email1.png" class="job-img">
        <div class="job-btn2">
          <form action="contact@taqwa.io" method="post" enctype="text/plain" style="position: relative;height: 100%;">
            <input type="submit" style="width: 100%;height: 100%;position: absolute;left: 0;top: 0;opacity: 0;" />
          </form>
        </div>
      </div>
      <div class="swiper-container job-swiper job-swiper2 swiper-no-swiping">
        <div class="swiper-wrapper">
          <div class="job-box swiper-slide">
            <img src="/static/img/job-1-3.png" class="job-img">
          </div>
          <div class="job-box swiper-slide">
            <img src="/static/img/job-2-3.png" class="job-img">
          </div>
        </div>
      </div>
    </div>
    <div class="contact-block absolute" v-show="initOk" :class="[currentMenu=='contact'?'doshow2':'dohide']">
      <div class="contack-box">
        <img src="/static/img/contact-1.jpg" class="contact-img">
        <div class="bgs-box">
          <div class="my-swiper swiper-container bgs swiper-no-swiping">
            <div class="swiper-wrapper">
              <div class="bgs-img-box swiper-slide" v-for="num of 5" :key="num">
                <img :src="`/static/img/concat-bgs${num}.png`" class="bgs-img">
              </div>
            </div>
          </div>
          <div class="bgs-btns">
            <img src="/static/img/job-arrow.png" class="bgs-btn left" @click="bgsChange(-1)">
            <img src="/static/img/job-arrow.png" class="bgs-btn" @click="bgsChange(1)">
          </div>
        </div>
        <div class="contack-btn1" @click="toTiktok()"></div>
        <div class="map-box" id="googleMap">
          <img src="/static/img/concat-map.png" v-if="!map" style="width: 100%;height: 100%;">
        </div>
        <div class="to-qingzhen contack" @click="tabChange('qingzhen')">
          <div class="to-qingzhen-left">
            <img src="/static/img/icon_qingzhen.png">
            <div class="to-qingzhen-text">PARTNER MOSQUES</div>
          </div>
          <img src="/static/img/tip-arrow.png" class="tip-right">
        </div>
      </div>
    </div>
    <div class="qingzhen-block absolute" v-show="initOk" :class="[currentMenu=='qingzhen'?'doshow2':'dohide']">
      <img src="/static/img/qingzhen-logo.png" class="qingzhen-img qingzhen-logo">
      <img src="/static/img/qingzhen-1.png" class="qingzhen-img">
      <img src="/static/img/qingzhen-2.png" class="qingzhen-img">
      <img src="/static/img/qingzhen-3.png" class="qingzhen-img">
      <img src="/static/img/qingzhen-4.png" class="qingzhen-img">
      <img src="/static/img/qingzhen-5.png" class="qingzhen-img">
      <img src="/static/img/qingzhen-bottom.png" class="qingzhen-bottom">
    </div>
  </div>
</template>
<style scoped>
.job-center{
  position: relative;
  padding: 0 16px;
  box-sizing: border-box;
}
.job-btn3{
  position: absolute;
  left: 8%;
  top: 35.5%;
  width: 40%;
  height: 29px;
}
.job-btn2{
  position: absolute;
  left: 32%;
  top: 35.5%;
  width: 32%;
  height: 20px;
}
.headBG{
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.headBG6{
  background-color: #0BBE62;
}
.headBG7{
  background-color: #C7954A;
}
.headBG8{
  background-color: #F08F13;
}
.headBG9{
  background-color: #E42A1C;
}
.headBG10{
  background-color: #3DAFFF;
}
.boxBG6{
  background-color: #043f22;
  color: #0BBE62;
}
.boxBG7{
  background-color: #453119;
  color: #C7954A;
}
.boxBG8{
  background-color: #532f07;
  color: #F08F13;
}
.boxBG9{
  background-color: #4f0e0a;
  color: #E42A1C;
}
.boxBG10{
  background-color: #153a58;
  color: #3DAFFF;
}
.ct-color6{
  color: #A9FF13;
}
.ct-color7{
  color: #F3B92C;
}
.ct-color8{
  color: #F3B92C;
}
.ct-color9{
  color: #C7A24A;
}
.ct-color10{
  color: #3DAFFF;
}
.brand-infos{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.brand-title{
  font-size: 30px;
  font-family: BonaNova-Bold;
  line-height: 30px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.brand-ct1{
  margin-top: 10px;
  width: calc(100% - 32px);
  margin-left: 16px;
  font-size: 12px;
  font-family: BonaNova-Regular;
  line-height: 20px;
  color: #fff;
}
.brand-ct2{
  width: calc(100% - 32px);
  font-size: 12px;
  font-family: BonaNova-Regular;
  line-height: 20px;
  margin: 16px;
}
.brand-btrect{
  width: calc(100% - 32px);
  margin-left: 16px;
  height: 164px;
}
.brand-btrect-tip{
  width: calc(100% - 32px);
  margin-left: 16px;
  height: 36px;
}
.center-box{
  bottom: 0;
}



.brand-8-text{
  position: absolute;
  left: 4.44%;
  bottom: 9%;
  font-size: 18px;
  font-family: BonaNova-Bold;
  line-height: 26px;
  width: 90%;
}
.absolute{
  position: absolute;
  left: 0;
  top: 0;
}
.doLeftIn{
  animation: doLeftIn .8s ease .2s both;
}
@keyframes doLeftIn{
  0% {
    opacity: 0;
    transform: translateX(-50%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.doRightOut{
  animation: doRightOut .8s ease .2s both;
}
@keyframes doRightOut{
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateX(100%);
  }
}

.doRightIn{
  animation: doRightIn .8s ease .2s both;
}
@keyframes doRightIn{
  0% {
    opacity: 0;
    transform: translateX(50%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.doLeftOut{
  animation: doLeftOut .8s ease .2s both;
}
@keyframes doLeftOut{
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateX(-100%);
  }
}
</style>
<script>
export default {
  name: 'Home',
  data () {
    return {
      menuList: [
        {id: "home",name:"HOME"},
        {id: "product",name:"PRODUCT"},
        {id: "brand",name:"BRAND"},
        {id: "job",name:"JOB"},
        {id: "contact",name:"CONTACT"}
      ],
      initOk: false,
      menuOpen: false,
      wheeling: false,
      currentMenu: 'home',
      showHomePage: false,
      currentHome: 1,
      carouselIdx: 0,
      homeSwiper: null,
      carouselHeight: 246,
      currentProductNum: 1,//产品
      currentProductBgNum: 1,//产品背景色
      currentProductColorNum: 1,//腕带色
      currentProductPMNum: 1,//屏幕内容
      diwenH: 374,
      showBrandPage: false,
      currentBrandNum: 0,//品牌页码
      headBGH: 60,
      map: null,
      centerboxH: 180,
      centerboxbgH: 164,
      centerboxbgTipH: 36,
      jobSwiper1: null,
      jobSwiper2: null,
      carouselJobIdx: 0,
      contactSwiper: null,
    }
  },
  mounted(){
    let that = this;
    that.carouselHeight = window.innerWidth*0.91*246/328;
    that.diwenH = window.innerWidth*374/360;
    that.headBGH = window.innerWidth*60/360;
    that.centerboxH = window.innerHeight*180/800;
    that.centerboxbgH = window.innerHeight*164/800;
    that.centerboxbgTipH = window.innerHeight*36/800;
    that.tabChange("home");
    setTimeout(() => {
      that.initOk = true;
      that.initSwiper();
    }, 1000);

    var startY, endY;
    var element = document.getElementById('contentBody');
    element.addEventListener('touchstart', function(e) {
      if(that.currentMenu=='home'||that.currentMenu=='brand'){
        startY = e.touches[0].clientY;
      }
    }, false);
    var direction = 0;
    element.addEventListener('touchmove', function(e) {
      if(that.currentMenu=='home'||that.currentMenu=='brand'){
        endY = e.touches[0].clientY;
        if(endY > startY+50){
          direction = -1;
        }else if(endY < startY-50){
          direction = 1;
        }
      }
    }, false);
    element.addEventListener('touchend', function(e) {
      if(that.currentMenu=='home'&&!that.wheeling&&direction){
        that.wheeling = true;
        setTimeout(() => {
          if(direction==-1){
            if(that.currentHome>1){
              that.currentHome--;
            }else{
              that.currentHome = 5;
            }
          }else if(direction==1){
            if(that.currentHome<5){
              that.currentHome++;
            }else{
              that.currentHome = 1;
            }
          }
          that.wheeling = false;
          direction = 0;
        }, 500);
      }else if(that.currentMenu=='brand'&&!that.wheeling&&direction){
        that.wheeling = true;
        setTimeout(() => {
          if(direction==-1){
            if(that.currentBrandNum>1){
              that.currentBrandNum--;
            }else{
              that.currentBrandNum = 12;
            }
          }else if(direction==1){
            if(that.currentBrandNum<12){
              that.currentBrandNum++;
            }else{
              that.currentBrandNum = 1;
            }
          }
          that.wheeling = false;
          direction = 0;
        }, 500);
      }
    }, false);
  },
  methods: {
    initSwiper(){
      let that = this;
      that.homeSwiper = new Swiper('.home-swiper',{
        loop : true,
        onSlideChangeEnd: function(evt){
          that.carouselIdx = evt.realIndex;
        }
      });
    },
    setCarouselIdx(idx){
      this.carouselIdx = idx;
      this.homeSwiper.slideTo(this.carouselIdx);
    },
    jobSwiperChange(type){
      if(type==-1){
        this.jobSwiper1.slidePrev();
        this.jobSwiper2.slidePrev();
      }else{
        this.jobSwiper1.slideNext();
        this.jobSwiper2.slideNext();
      }
    },
    bgsChange(type){
      if(type==-1){
        this.contactSwiper.slidePrev();
      }else{
        this.contactSwiper.slideNext();
      }
    },
    brandChangePage(){
      if(this.currentBrandNum<12){
        this.currentBrandNum++;
      }else{
        this.currentBrandNum = 1;
      }
      let that = this;
      setTimeout(() => {
        that.$refs.brandRef.scrollTo({top:0,behavior:'smooth'});
      }, 300);
    },
    changeWatchBG(type){
      if(this.currentProductBgNum==1){
        if(type==1){
          this.currentProductBgNum = 2;
        }else{
          this.currentProductBgNum = 3;
        }
      }else if(this.currentProductBgNum==2){
        if(type==1){
          this.currentProductBgNum = 1;
        }else{
          this.currentProductBgNum = 3;
        }
      }else if(this.currentProductBgNum==3){
        if(type==1){
          this.currentProductBgNum = 1;
        }else{
          this.currentProductBgNum = 2;
        }
      }
    },
    tabChange(menu){//头部导航
      let that = this;
      if(that.currentMenu!=menu){
        that.currentMenu = menu;
      }
      that.menuOpen = false;
      that.wheeling = false;
      if(menu=='home'){
        that.currentHome = 1;
        that.carouselIdx = 0;
        setTimeout(() => {
          that.homeSwiper&&that.homeSwiper.slideTo(that.carouselIdx);
          that.showHomePage = true;
        }, 1000);
      }else{
        that.showHomePage = false;
      }
      if(menu=='brand'){
        that.currentBrandNum = 1;
        setTimeout(() => {
          that.showBrandPage = true;
        }, 800);
      }else{
        that.currentBrandNum = 0;
        that.showBrandPage = false;
      }
      if(menu=='contact'){
        setTimeout(() => {
          if(!that.contactSwiper){
            that.contactSwiper = new Swiper('.bgs',{
              loop : true
            });
          }
        }, 1000);
        if(google&&!that.map){
          var mapProp = {
            center: new google.maps.LatLng(-6.184757360602209, 106.78518602700933),
            zoom:15,
            mapTypeId:google.maps.MapTypeId.ROADMAP
          };
          that.map = new google.maps.Map(document.getElementById("googleMap"), mapProp);
          var marker = new google.maps.Marker({
            position: new google.maps.LatLng(-6.184757360602209, 106.78518602700933),
          });
          marker.setMap(that.map);
          console.log(that.map)
        }
      }
      if(menu=='job'){
        setTimeout(() => {
          if(!that.jobSwiper1){
            that.jobSwiper1 = new Swiper('.job-swiper1',{
              loop : true,
              autoHeight: true
            });
            that.jobSwiper2 = new Swiper('.job-swiper2',{
              loop : true,
              autoHeight: true
            });
          }
        }, 500);
      }
    },
    nextHomePage(){
      if(this.currentHome==5){
        this.currentHome = 1;
      }else{
        this.currentHome++;
      }
    },
    toShopee(){
      window.open('https://shopee.co.id/taqwasmartwatch?categoryId=100013&entryPoint=ShopByPDP&itemId=25679391607');
    },
    toTiktok(){
      window.open('https://vt.tokopedia.com/t/ZSYgN5or4/');
    },
  }
}
</script>


